@import url("https://fonts.googleapis.com/css?family=Orbitron:400,700&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.App {
  font-family: "Orbitron", sans-serif;
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.buttons-container {
  margin: 2rem;
}
button {
  font-family: "Orbitron", sans-serif;
  margin: 1rem;
  font-size: 2rem;
  font-weight: 900;
  padding: 1rem;
  border: 5px solid #61dafb;
  border-radius: 25px;
}
button:hover {
  background-color: #282c34;
  color: #61dafb;
}
.result {
  border: 5px solid #61dafb;
  border-radius: 25px;
  width: 40vw;
}
.display {
  font-size: 4rem;
  padding: 1rem 2rem;
  color: #282c34;
  background-color: white;
  border-radius: 20px;
}
